import React,{Component} from 'react';
import {uris,tppappname,compliance} from '../../constants';
import {withRouter} from 'react-router-dom';
import { FaBriefcase } from "react-icons/fa";
import PaperComponent from '../../components/Paper';
import Typography from '@material-ui/core/Typography';

class CompliancePage extends Component{
    constructor(props){
        super(props)
    }

    render(){
        const buttonData={
            btn1:{
                label:"Close",
                uri:uris.welcome
            },
            btn2:{
                label:"Next",
                uri: uris.complianceConfirm
            }
        }
        return(
            <div className="compliance">
                 <div className="icon" onClick={this.redirect}>
                    <FaBriefcase />
                </div>
                <div className="title">
                    {tppappname}
                </div>
                <div className="body">
                    <PaperComponent buttonData={buttonData} router={this.props.history}>
                        <Typography component="div">
                            <div style={{"fontWeight" : "bold"}}>
                                {compliance.title}
                            </div> 
                            <div style={{marginTop:"30px"}}>
                                {compliance.description}
                            </div>
                            <div>
                                <ul style={{"paddingLeft" : "3%"}}>
                                {
                                    compliance.points.map((data,index)=>{
                                        return (
                                            <li key={index}
                                            dangerouslySetInnerHTML={{__html : data}}>
                                            </li>
                                        )
                                    })
                                }
                                </ul>
                            </div>
                        </Typography>
                    </PaperComponent>
                </div>
            </div>
        )
    }
}


export default withRouter(CompliancePage);