import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import MuiExpansionPanel from '@material-ui/core/ExpansionPanel';
import MuiExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import MuiExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import { MdAdd as ExpandMoreIcon,MdRemove as ExpandLessIcon } from "react-icons/md";
import {consent} from '../constants';

const ExpansionPanel = withStyles({
  root: {
    //border: '1px solid rgba(0,0,0,.125)',
    boxShadow: 'none',
    '&:not(:last-child)': {
      borderBottom: 0,
    },
    '&:before': {
      display: 'none',
    },
    padding:0
  },
  expanded: {
    margin: 'auto',
  },
})(MuiExpansionPanel);

const ExpansionPanelSummary = withStyles({
  root: {
    //backgroundColor: 'rgba(0,0,0,.03)',
    backgroundColor:"white",
    //borderBottom: '1px solid rgba(0,0,0,.125)',
    marginBottom: -1,
    minHeight: 16,
    '&$expanded': {
      minHeight: 56,
    },
    paddingLeft:"0px",
    marginLeft:"0px"
  },
  content: {
    '&$expanded': {
      margin: '12px 0',
    },
  },
  expanded: {},
})(props => <MuiExpansionPanelSummary {...props} />);

ExpansionPanelSummary.muiName = 'ExpansionPanelSummary';

const ExpansionPanelDetails = withStyles(theme => ({
  root: {
    //padding: theme.spacing.unit * 1,
    padding:"0px"
  },
}))(MuiExpansionPanelDetails);

class CustomizedExpansionPanel extends React.Component {
  state = {
    expanded: '',
  };

  handleChange = panel => (event, expanded) => {
    this.setState({
      expanded: expanded ? panel : false,
    });
  };

  render() {
    const { expanded } = this.state;
    
    const renderData = consent.part1.points.map((data,index)=>{
        return(
            <ExpansionPanel
                key={index}
                square
                expanded={expanded === 'panel'+index}
                onChange={this.handleChange('panel'+index)}
            >
                <ExpansionPanelSummary
                    expandIcon={ expanded === 'panel'+index ? <ExpandLessIcon /> : <ExpandMoreIcon />}
                >
                    <Typography><b>{data.title}</b></Typography>
                </ExpansionPanelSummary>
                <ExpansionPanelDetails>
                    <Typography component="div">
                    <div style={{fontStyle:"italic"}}>{data.subtitle}</div>
                    <ul>
                        {data.children.map((childdata,childindex)=>{
                            return(
                                <li key={childindex}>{childdata}</li>
                            )
                        })}
                    </ul>
                    </Typography>
                </ExpansionPanelDetails>
            </ExpansionPanel>
        )
    })


    return (
      <div>
        {renderData}
      </div>
    );
  }
}

export default CustomizedExpansionPanel;
