import React,{Component} from 'react';
import TitleComponent from './../../components/Title';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import {withRouter} from 'react-router-dom';
import {uris,bankredirectfrom} from '../../constants';
import PaperComponent from '../../components/Paper';
import Button from '@material-ui/core/Button';


import { MdCheck } from "react-icons/md";

const styles = theme => ({
    root: {
      ...theme.mixins.gutters(),
      paddingTop: theme.spacing.unit * 2,
      paddingBottom: theme.spacing.unit * 2,
    },
    button: {
        margin: theme.spacing.unit,
        backgroundColor:"#6200ee",
        color:"white"
    },
    input: {
        display: 'none',
    },
});

class RedirectFromPage extends Component{
    constructor(props){
        super(props)
    }
    redirect=(uri)=>{
        this.props.history.push(uri);
    }
    render(){
        const { classes } = this.props;

        const buttonData={
            btn1:{
                label:"Close",
                uri: uris.welcome
            },
            btn2:{
                label:"Proceed",
                uri: uris.accountdetails
            }
        }
        return(
            <div className="redirectfrompage">
                <TitleComponent style={{backgroundColor:"#6200ee"}}/>
                <div className="body">
                    <div>
                        <PaperComponent 
                        style={{padding:0}}
                        router={this.props.history}>
                            <Typography component="div">
                                <div className="icon">
                                    <MdCheck />
                                </div>
                                <div className="text1">
                                    {bankredirectfrom.text1}
                                </div>
                            </Typography>
                        </PaperComponent>
                    </div>
                    <div style={{
                        display:"flex",
                        paddingBottom:"40px",
                        justifyContent:"center",alignItems:"center",
                        margin:"0 auto"}}>
                            <Button onClick={()=>this.redirect(buttonData.btn1.uri)}
                                fullWidth variant="text" color="primary" className={classes.button}>
                                {buttonData.btn1.label}
                            </Button>
                            <Button onClick={()=>this.redirect(buttonData.btn2.uri)}
                            fullWidth variant="text" color="primary" className={classes.button}>
                                {buttonData.btn2.label}
                            </Button>
                    </div>
                </div>
            </div>
        )
    }
}


export default withStyles(styles)(withRouter(RedirectFromPage));