import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import { IoIosStar } from "react-icons/io";

const styles = theme => ({
  root: {
    //width: '100%',
    //maxWidth: 400,
    //width:360
    //backgroundColor: theme.palette.background.paper,
  },
  inline: {
    display: 'inline',
  },
});

function AlignItemsList(props) {
  const { classes,filteredBanks,redirect } = props;
  const renderList = filteredBanks.map((data,index)=>{
      return(
            <ListItem  
                onClick={() =>redirect(data)}
                key={index}
                className="listitemcustom"
                alignItems="flex-start" 
                style={{cursor:"pointer",
                margin: "8px 0px",
                backgroundColor:"white",
                }}>
                <ListItemAvatar>
                    {/* <Avatar alt="Remy Sharp" src="/assets/lacoste.png" /> */}
                    <div style={{color: data.color,fontSize:"20px"}}>
                    <IoIosStar />
                    </div>
                </ListItemAvatar>
                <ListItemText
                    primary={data.label}
                />
            </ListItem>
      )
  })
  
  return <List className={classes.root}>
  {renderList}
</List>
}

AlignItemsList.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(AlignItemsList);
