import React,{Component} from 'react';
import {uris,tppappname,compliance,complianceConfirm} from '../../constants';
import {withRouter} from 'react-router-dom';
import { FaBriefcase } from "react-icons/fa";
import PaperComponent from '../../components/Paper';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';

class ComplianceConfirmPage extends Component{
    constructor(props){
        super(props)
    }

    render(){
        const buttonData={
            btn1:{
                label:"No, close",
                uri:uris.welcome
            },
            btn2:{
                label:"Yes, proceed",
                uri: uris.consent
            }
        }
        return(
            <div className="complianceconfirm">
                 <div className="icon" onClick={this.redirect}>
                    <FaBriefcase />
                </div>
                <div className="title">
                    {tppappname}
                </div>
                <div className="body">
                    <PaperComponent buttonData={buttonData} router={this.props.history}>
                        <Typography component="div">
                            <div style={{"fontWeight" : "bold",marginTop:"40px"}}>
                                {complianceConfirm.text1}
                            </div> 
                            <div>
                                <ul style={{"paddingLeft" : "3%"}}>
                                {
                                    complianceConfirm.points.map((data,index)=>{
                                        return (
                                            <li key={index}
                                            dangerouslySetInnerHTML={{__html : data}}>
                                            </li>
                                        )
                                    })
                                }
                                </ul>
                            </div>
                            <div style={{marginTop:"0px"}}>
                                {complianceConfirm.text2}
                            </div>
                            <div>
                                <Divider variant="fullWidth" style={{margin:"25px"}} />
                            </div>
                            <div dangerouslySetInnerHTML={{__html : complianceConfirm.text3}}></div>
                            <div style={{marginTop:"30px"}} dangerouslySetInnerHTML={{__html : complianceConfirm.text4}}></div>
                        </Typography>
                    </PaperComponent>
                </div>
            </div>
        )
    }
}


export default withRouter(ComplianceConfirmPage);