import React,{Component} from 'react';
import HeaderComponent from '../../components/Header';
import StepperComponent from '../../components/Stepper';
import PaperComponent from '../../components/Paper';
import {withRouter} from 'react-router-dom';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import TitleComponent from './../../components/Title';
import Button from '@material-ui/core/Button';
import {uris,listOfbanks,bankRedirectTo} from '../../constants';


const styles = theme => ({
    root: {
      ...theme.mixins.gutters(),
      paddingTop: theme.spacing.unit * 2,
      paddingBottom: theme.spacing.unit * 2,
    },
    button: {
        margin: theme.spacing.unit,
        backgroundColor:"#6200ee",
        color:"white"
    },
    input: {
        display: 'none',
    },
});

class RedirecttoBankPage extends Component{
    constructor(props){
        super(props)
    }
    componentDidMount(){
        console.log("RedirecttoBankPage componentDidMount ")
        console.log("this.props ",this.props.match.params.bankid)
    }
    redirect=(uri)=>{
        console.log("RedirecttoBankPage  data ",uri)
        if(uri.search("http")>=0){
            window.location.href=uri;
        }
        else{
            this.props.history.push(uri);
        }

    }
    render(){
        const { classes } = this.props;
        const buttonData={
            btn1:{
                label:"Cancel",
                uri:uris.welcome
            },
            btn2:{
                label:"Proceed",
                uri: uris.ext_redirecttobank
            }
        }
        const getBank = listOfbanks.find((data,index)=> 
        data.value === this.props.match.params.bankid);
        return(
            <div className="redirecttoBankPage">
                <HeaderComponent  />
                <div className="stepper" style={{backgroundColor:"#ededed"}}>
                    <StepperComponent activeStep={3} />
                </div>
                <div className="body">
                    <div>
                        <PaperComponent 
                        style={{padding:0}}
                        router={this.props.history}>
                            <Typography component="div">
                                <div>
                                    <TitleComponent style={{backgroundColor:"#6200ee"}}/>
                                </div>
                                <div 
                                style={{textAlign:"center",paddingTop:"40px"}}
                                dangerouslySetInnerHTML={{__html : bankRedirectTo.text0.replace("{bankName}",getBank.label)}}></div>
                                <div 
                                style={{textAlign:"center",padding:"10px 0px 40px 0px"}}
                                dangerouslySetInnerHTML={{__html : bankRedirectTo.text1.replace("{bankName}",getBank.label)}}></div>
                            </Typography>
                        </PaperComponent>
                    </div>
                    <div style={{
                        display:"flex",
                        paddingBottom:"40px",
                        //width:400,
                        justifyContent:"center",alignItems:"center",
                        margin:"0 auto"}}>
                            <Button onClick={()=>this.redirect(buttonData.btn1.uri)}
                                fullWidth variant="text" color="primary" className={classes.button}>
                                {buttonData.btn1.label}
                            </Button>
                            <Button onClick={()=>this.redirect(buttonData.btn2.uri)}
                            fullWidth variant="text" color="primary" className={classes.button}>
                                {buttonData.btn2.label}
                            </Button>
                    </div>
                </div>
            </div>
        )
    }
}


export default withStyles(styles)(withRouter(RedirecttoBankPage));