import React,{Component} from 'react';
import { MdSecurity } from "react-icons/md";
import {uris,tppappname,consent,listOfbanks,consent_bank} from '../../constants';
import HeaderComponent from '../../components/Header';
import Divider from '@material-ui/core/Divider';
import PaperComponent from '../../components/Paper';
import {withRouter} from 'react-router-dom';
import Typography from '@material-ui/core/Typography';
import ExpansionPanelComponent from '../../components/ExpansionPanel';
import Button from '@material-ui/core/Button';
import { withStyles } from '@material-ui/core/styles';
import StepperComponent from '../../components/Stepper';
import SearchBarComponent from './../../components/SearchBar';

import ListItemComponent from './../../components/ListItems';
const styles = theme => ({
    root: {
      ...theme.mixins.gutters(),
      paddingTop: theme.spacing.unit * 2,
      paddingBottom: theme.spacing.unit * 2,
    },
    button: {
        margin: theme.spacing.unit,
        backgroundColor:"#488fe1",
        color:"white"
    },
    input: {
        display: 'none',
    },
  });


class BankPage extends Component{
    constructor(props){
        super(props)
        this.state={
            filteredBanks : []
        }
    }
    redirect=(data)=>{
        console.log("PaperComponent redirect data ",data)
        this.props.history.push(uris.redirecttobank.path+data.value);

    }
    updateSearchText=(text)=>{
        console.log("updateSearchText text ",text)
        if(text.length>1){
            this.setState({
                filteredBanks : listOfbanks.filter((data,index)=>
                 data.label.toLowerCase().search(text.toLowerCase()) >=0
                )
            })
        }
        else{
            this.setState({
                filteredBanks:[]
            })
        }

        console.log("filteredBanks ",this.state.filteredBanks)
    }
    render(){
        const { classes } = this.props;

        const buttonData={
            btn1:{
                label:"Cancel",
                uri:uris.welcome
            },
            btn2:{
                label:"Confirm",
                uri: uris.consent
            }
        }
        return(

            <div className="consentbankpage">
                <HeaderComponent />
                <div className="stepper" style={{backgroundColor:"#ededed"}}>
                    <StepperComponent activeStep={1} />
                </div>
                <div className="body">
                    <div>
                        <PaperComponent router={this.props.history}>
                            <Typography component="div">
                                <div style={{

                                    display:"flex",
                                    flexDirection:"column",
                                    alignItems:"center",
                                    marginBottom:"30px"}}>
                                    <div className="icon">
                                        <MdSecurity/>
                                    </div>
                                    <div className="title" style={{fontSize:"17px",fontWeight:"500"}}>
                                        Select your bank
                                    </div>
                                    <div>
                                        <ul>
                                            {
                                                consent_bank.text1.map((data,index)=>{
                                                    return(
                                                        <li key={index}>
                                                        {data}
                                                        </li>
                                                    )
                                                })
                                            }
                                        </ul>
                                    </div>
                                </div>
                                <div style={{width:"100%",    
                                display: "flex",
                                flexDirection:"column",
                                alignItems:"center",
                                justifyContent: "center"}}>
                                    <div>
                                        <SearchBarComponent updateSearchText={this.updateSearchText} />
                                    </div>
                                    <div>
                                        <ListItemComponent 
                                        redirect={this.redirect}
                                        filteredBanks={this.state.filteredBanks}/>
                                    </div>
                                </div>
                            </Typography>
                        </PaperComponent>
                    </div>
                    {/* <div style={{
                        display:"flex",
                        marginTop:"20px",
                        width:400,
                        justifyContent:"center",alignItems:"center",
                        margin:"0 auto"}}>
                            <Button onClick={()=>this.redirect("")}
                                fullWidth variant="text" color="primary" className={classes.button}>
                                {buttonData.btn1.label}
                            </Button>
                            <Button onClick={()=>this.redirect("")}
                            fullWidth variant="text" color="primary" className={classes.button}>
                                {buttonData.btn2.label}
                            </Button>
                    </div> */}
                </div>
            </div>
        )
    }
}


export default withStyles(styles)(withRouter(BankPage));