import React from 'react';
import {BrowserRouter,HashRouter,Route} from 'react-router-dom';
import WelcomePage from './pages/welcome';
import WelcomeConfirmPage from './pages/welcome/confirm';
import CompliancePage from './pages/compliance';
import ComplianceConfirmPage from './pages/compliance/confirm';
import ConsentPage from './pages/consent';
import ConsentConfirmPage from './pages/consent/confirm';

import BankPage from './pages/consent/Bank';
import RedirecttoBankPage from './pages/bank/Redirectto';
import RedirectFromBankPage from './pages/bank/Redirectfrom';
import AccountDetailsPage from './pages/account';

import {uris} from './constants';
 
function App() {
  return (
    <HashRouter basename="/abank">
      <Route path={uris.welcome} exact component={WelcomePage} />
      <Route path={uris.welcomeconfirm} exact component={WelcomeConfirmPage} />
      <Route path={uris.compliance} exact component={CompliancePage} />
      <Route path={uris.complianceConfirm} exact component={ComplianceConfirmPage} />
      <Route path={uris.consent} exact component={ConsentPage} />
      <Route path={uris.consentconfirm} exact component={ConsentConfirmPage} />

      <Route path={uris.consentbank} exact component={BankPage} />
      <Route path={uris.redirecttobank.uri} exact component={RedirecttoBankPage} />
      <Route path={uris.redirectfrombank.uri} exact component={RedirectFromBankPage} />
      <Route path={uris.accountdetails} exact component={AccountDetailsPage} />

    </HashRouter>
  );
}

export default App;
