import React,{Component} from 'react';
import { MdSecurity } from "react-icons/md";
import {uris,tppappname,consent,accountdetails} from '../../constants';
import HeaderComponent from '../../components/Header';
import Divider from '@material-ui/core/Divider';
import PaperComponent from '../../components/Paper';
import {withRouter} from 'react-router-dom';

import Button from '@material-ui/core/Button';
import { withStyles } from '@material-ui/core/styles';
import TableComponent from './../../components/Table';
const styles = theme => ({
    root: {
      ...theme.mixins.gutters(),
      paddingTop: theme.spacing.unit * 2,
      paddingBottom: theme.spacing.unit * 2,
    },
    button: {
        margin: theme.spacing.unit,
        backgroundColor:"#6200ee",
        color:"white"
    },
    input: {
        display: 'none',
    },
  });


class ConsentPage extends Component{
    constructor(props){
        super(props)
    }
    redirect=(uri)=>{
        console.log("PaperComponent redirect uri ",uri)
        this.props.history.push(uri);

    }
    render(){
        const { classes } = this.props;

        const buttonData={
            btn1:{
                label:"Done",
                uri:uris.welcome
            }
        }
        return(

            <div className="accountdetailspage">
                <HeaderComponent />
                <div className="body">
                    <div>
                    
                            <div style={{
                                display:"flex",
                                flexDirection:"column",
                                alignItems:"center"}}>
                                <div className="icon">
                                    <MdSecurity/>
                                </div>
                                <div className="title">
                                    Account Information Received
                                </div>
                            </div>
                           <div>
                                {
                                    accountdetails.data.map((data1,index1)=>{
                                        return (
                                            <div key={index1}>
                                                <TableComponent 
                                                    title={data1.title} 
                                                    content={data1.children}
                                                />
                                            </div>
                                        )
                                    })
                                }
                            </div>
                            
                        
                        <div style={{display:"flex",justifyContent:"center",padding: "30px 0px 30px 0px",margin:"0 auto"}}>
                            <Button onClick={()=>this.redirect(buttonData.btn1.uri)}
                                 variant="text" color="primary" className={classes.button}>
                                {buttonData.btn1.label}
                            </Button>
                        </div>
                    </div>
                    
                </div>
            </div>
        )
    }
}


export default withStyles(styles)(withRouter(ConsentPage));