import React,{Component} from 'react';
import { FaRegHandshake } from "react-icons/fa";
import {withRouter} from 'react-router-dom';
import {uris,tppappname,tppappDescription} from '../../constants';
import {welcometotppapp} from './../../constants';
import PaperComponent from './../../components/Paper';
import Typography from '@material-ui/core/Typography';

class WelcomeConfirmPage extends Component{
    constructor(props){
        super(props)
    }
    redirect=()=>{
        this.props.history.push(uris.welcomeconfirm);
    }
    render(){
        const buttonData={
            btn1:{
                label:"Close",
                uri:uris.welcome
            },
            btn2:{
                label: "Proceed",
                uri : uris.compliance
            }
        }
        return(
            <div className="welcomeconfirmpage">
                 <div className="icon" onClick={this.redirect}>
                    <FaRegHandshake />
                </div>
                <div className="title">
                    {welcometotppapp}
                </div>
                <div className="body">
                    <PaperComponent buttonData={buttonData} router={this.props.history}>
                        <Typography component="p">
                            <span style={{"fontWeight" : "bold"}}>
                                {tppappname}
                            </span> 
                            {tppappDescription}
                        </Typography>
                    </PaperComponent>
                </div>
            </div>
        )
    }
}


export default withRouter(WelcomeConfirmPage);