
import React from 'react';

import { withStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';

const styles = theme => ({
    root: {
      ...theme.mixins.gutters(),
      paddingTop: theme.spacing.unit * 2,
      paddingBottom: theme.spacing.unit * 2,

    },
    button: {
        margin: theme.spacing.unit,
        backgroundColor:"#6200ee",
        color:"white"
    },
    input: {
        display: 'none',
    },
  });

class PaperComponent extends React.Component{

    redirect=(uri)=>{
        console.log("PaperComponent redirect uri ",uri)
        this.props.router.push(uri);

    }
    render(){
        const { classes,buttonData } = this.props;
        return(
            <Paper 
            style={{...this.props.style}}
            className={classes.root} elevation={0}>
                {this.props.children}
                {
                    (this.props.buttonData !=null) ? 
                    <div style={{display:"flex",marginTop:"20px"}}>
                        <Button onClick={()=>this.redirect(this.props.buttonData.btn1.uri)}
                            fullWidth variant="text" color="primary" className={classes.button}>
                            {this.props.buttonData.btn1.label}
                        </Button>
                        <Button onClick={()=>this.redirect(this.props.buttonData.btn2.uri)}
                        fullWidth variant="text" color="primary" className={classes.button}>
                            {this.props.buttonData.btn2.label}
                        </Button>
                    </div> : null
                }
                
                
            </Paper>
        )
    }
}

export default withStyles(styles)(PaperComponent);