import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import SimpleDialogComponent from './SimpleDialog';

const CustomTableCell = withStyles(theme => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const styles = theme => ({
  root: {
    width: '100%',
    marginTop: theme.spacing.unit * 3,
    overflowX: 'auto',
  },
  table: {
    minWidth: 400,
  },
  row: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.background.default,
    },
    textAlign:"left"
  },
});

let id = 0;
function createData(name, calories, fat, carbs, protein) {
  id += 1;
  return { id, name, calories, fat, carbs, protein };
}

const rows = [
  createData('Frozen yoghurt', 159, 6.0, 24, 4.0),
  createData('Ice cream sandwich', 237, 9.0, 37, 4.3),
  createData('Eclair', 262, 16.0, 24, 6.0),
  createData('Cupcake', 305, 3.7, 67, 4.3),
  createData('Gingerbread', 356, 16.0, 49, 3.9),
];

function CustomizedTable(props) {
  const { classes } = props;

  return (
    <Paper className={classes.root}>
      <Table className={classes.table}>
        <TableHead>
          <TableRow>
          <TableCell style={{width:"150px"}}>
            
            <div style={{padding:"5px 0px",fontSize:"16px"}}>{props.title}</div>
            <div style={{padding:"5px 0px"}}>{props.content[0].td8}</div>
          </TableCell>
          <TableCell>
            
          </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {props.content.map((row,index2) => (
            
            <TableRow className={classes.row} key={index2}>
               <TableCell style={{width:"150px"}} align="left">
                   {row.td1}
               </TableCell>
               <TableCell style={{width:"250px"}} align="left">
                   {(row.td9 == "panel") ? 
                        <SimpleDialogComponent imagedata={row.td2}/>
                    : 
                        row.td2
                    }
               </TableCell>
            </TableRow>
          ))
          }
        </TableBody>
      </Table>
    </Paper>
  );
}

CustomizedTable.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(CustomizedTable);
