                            {/* <div>
                                <Divider variant="fullWidth" style={{margin:"25px"}} />
                            </div>
                            <div dangerouslySetInnerHTML={{__html : consent.part2.text1}}></div>
                            <div>
                                <ul style={{"paddingLeft" : "4%"}}>
                                {
                                    consent.part2.points.map((data,index)=>{
                                        return (
                                            <li key={index}
                                                dangerouslySetInnerHTML={{__html : data}}>
                                            </li>
                                        )
                                    })
                                }
                                </ul>
                            </div> */}
